@import "../variables";

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 645px){
        flex-direction: column;
        .header__cart{
          margin-top: 60px;
        }
    }
  }

  &__logo {
    display: flex;
   
    @media (max-width: 645px){
      flex-direction: column;
      align-items: center;

      img {
        width: 100px;
        margin-right: 0 !important;
      }

      h1 {
        text-align: center;
        margin: 20px 0 20px;
        font-size: 42px !important;
      }

      p {
        text-align: center;
        font-size: 28px;
      }
  }

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
